import fetch from 'isomorphic-unfetch';
import AppBar from '@material-ui/core/AppBar';
import ReactMarkdown from 'react-markdown';

class Skills extends React.Component {
  getSkillsByDomain = (domain) => {

  };

  render() {
    const { classes, hp, lang } = this.props;

    return (
      <div id="section-skills">
        <AppBar position="static" className={classes.appbar}>
          {hp['Titre compétences']}
        </AppBar>
        <ReactMarkdown source={hp['Texte compétences']} className={classes.markdownskills} />
        {domains.map(domain => (
          <h2>{lang === 'en' ? domain['Titre anglais'] : domain['Titre']}</h2>
        ))}
      </div>
    );
  }
}

Skills.getInitialProps = async function () {
  const strapiUrl = 'http://localhost:1337';
  const res = await fetch(`${strapiUrl}/domaines`);
  const data = await res.json();

  return {
    domains: data
  }
};

export default Skills;
